<template>
    <!-- <el-breadcrumb :separator="decollator">
            <el-breadcrumb-item  :to="routePath">{{crumbs[0]}} </el-breadcrumb-item>
            <el-breadcrumb-item>{{crumbs[1]}} </el-breadcrumb-item>
    </el-breadcrumb> -->
    <div class="crumb">
        <div class="crumb_lt">
             <SvgIcon icon-class="iconbiaoti" size="18px" />
            <span>{{crumbs[1]}}</span>
        </div>
        <div  class="crumb_rt">
            <slot name="right"></slot>
        </div>
    </div>
</template>

<script>
export default {
 name: 'Crumb',
 props:{
     data:{
         type:Array,
         default:() =>{
             return []
         }
     },
     decollator:{
         type:String,
         default:'/'
     },
     path:{
        type:String,
        default:''
     }
 },
 data() {
 return {
  crumbs:[]
 }
 },
 created(){},
 mounted(){
    this.initData() 
 },
 methods:{
     initData(){
        const self=this
        if(self.data.length ===0){
            self.crumbs=JSON.parse(localStorage.getItem('crumbs'))
            self.$emit('headInfo', self.crumbs, self.crumbs[1])
            //self.getRouteMeta()
        }else{
          self.crumbs=self.data
          self.$emit('headInfo', self.data,  self.data[1])
        }   
        
     },
     getRouteMeta(){
         const _self=this
         let num=0
         setTimeout(function cb(){
            if(_self.$route.meta.crumbs){
                _self.crumbs=_self.$route.meta.crumbs
                _self.$emit('headInfo', _self.$route.meta.crumbs, _self.$route.meta.crumbs[1])
            }else{
                  num++
                if(num < 40) setTimeout(cb,200)
            }
         },200)
     }  
 },
 computed:{
     routePath(){
        return this.path ? {path: this.path} :  ''
     }
 }
}
</script>

<style lang='scss' scoped>
.crumb{
    height:80px;
    background:rgba(248,248,248,1);
    .crumb_lt{
        display:inline-block;
        height:100%;
        line-height:80px;
        float:left;
        span{
            font-size:24px;
            font-weight:bold;
            color:#696969;
            margin-left:4px;
        }
    }
    .crumb_rt{
        display:inline-block;
        height:100%;
        line-height: 80px;
        float:right;
    }
}
</style>



