<template>
  <svg class="svg-icon" aria-hidden="true" :style="iconSize">
    <use :xlink:href="iconName"/>
  </svg>
</template>

<script>
export default {
  name: 'SvgIcon',
  props: {
    iconClass: {
      type: String,
      required: true
    },
    size:{
      type:String,
      default:''
    }
  },
  computed: {
    iconName() {
      let className=this.iconClass
      //if(this.iconClass.indexOf('-') === -1) className = `icon-${this.iconClass.substr(4, this.iconClass.length-1)}`          
      return `#${className}`
    },
    iconSize(){
      return this.size ? `font-size:${this.size}` : ''
    }
  }
}
</script>

<style scoped>
.svg-icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
  transform: rotate(0deg);
}
</style>



