// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import 'babel-polyfill'
import '../theme/index.css';
import ElementUI from 'element-ui';
import '@/assets/css/index.css'
import SvgIcon from './components/SvgIcon/index'
import Crumb from "./components/Crumb/index";
import { post, get, patch, put, deletes, API_URL } from './utils/request'

Vue.prototype.API_URL = API_URL
Vue.prototype.$post = post
Vue.prototype.$get = get
Vue.prototype.$patch = patch
Vue.prototype.$put = put
Vue.prototype.$deletes = deletes

Vue.directive('havaRole', {
    bind(el, binding) {
        // binding.value获取到的是传递到自定义指令中属性的值
        const roleList = localStorage.getItem('UrlStr')
        const flag = roleList.includes(binding.value)
        if (flag) {
            el.style.display = 'inline-block'
        } else {
            el.style.display = 'none'
        }
    }
})
Vue.prototype.$havaRole = function(role) {
    const roleList = localStorage.getItem('UrlStr')
    return roleList.includes(role)
}

Vue.config.productionTip = false
Vue.use(ElementUI);

Vue.component("SvgIcon", SvgIcon);
Vue.component("Crumb", Crumb);
//   路由拦截 检验token值、
/*router.beforeEach((to, from, next) => {
    if (to.path === '/' || to.path === '/resetPasswords' || to.path === '/test') {
        next()
    } else {
        if (localStorage.getItem('x_token')) {
            next();
        } else {
            next({ path: '/' })
        }
    }
});*/

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


